function LongMatrixProduct (m, n) {
  var l = m.length;
  var d = new Array(l).fill(0).map(x => new Array(l));
  console.log(d);
  function rec (start, end) {
    console.log('rec(', start, end, ')');
    if (d[start][end] !== undefined) {
      return d[start][end];
    }
    if (end - start === 0) {
      d[start][end] = 0;
      return d[start][end];
    }
    // if(end - start === 1){
    //  d[start][end] = m[start] * n[start] * n[end]; // mxn nxk => m*n*k
    //  return d[start][end]
    // }
    var min = Infinity;
    for (var i = start; i < end; i++) {
      min = Math.min(min, rec(start, i) + rec(i + 1, end) + m[start] * n[i] * n[end]);
    }
    d[start][end] = min;
    return d[start][end];
  }
  console.log(d);
  return rec(0, l - 1);
}

LongMatrixProduct([10, 100, 5], [100, 5, 50]);



